.infoBox{
flex:1;
cursor:pointer;
}
.infoBox:not(:last-child){
    margin-right: 15px;
}
.infoBox--selected{
    border-top:10px solid #4b2665;
}
.cases{
    color: #ff0000;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.total{
    color:  #333333;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.infoBox__cases--green{
    color: #3DD20F!important;;

}